/** @jsx jsx */
/* eslint-disable */
import { jsx } from "theme-ui";
import React from "react";
import { useEffect, useState } from "react";

import UsersList from "../components/UsersList";
import ErrorModal from "../../shared/components/UIElements/ErrorModal";
import LoadingSpinner from "../../shared/components/UIElements/LoadingSpinner";
import { useHttpClient } from "../../shared/hooks/http-hook";
import Card from "../../shared/components/UIElements/Card";
import TipoloLeaf from "../../shared/images/tipolo_leaf_trans.png";

const Users = () => {
  const { isLoading, error, sendRequest, clearError } = useHttpClient();
  const [loadedUsers, setLoadedUsers] = useState();

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const responseData = await sendRequest(
          process.env.REACT_APP_BACKEND_API_URL + "/users"
        );

        setLoadedUsers(responseData.users);
      } catch (err) {}
    };
    fetchUsers();
  }, [sendRequest]);

  const cardProps = {
    position: "relative",
    mt: 5,
    p: 0,
    boxShadow: "0",
    borderRadius: "0",
    overflow: "hidden",
    bg: "#4d4d4d",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    textAlign: "center",
  };

  return (
    <React.Fragment>
      <Card customStyle={cardProps}>
        <div sx={{ variant: "styles.listCenterHome" }}>
          <img
            src={TipoloLeaf}
            sx={{ variant: "styles.listCenterImageHome" }}
          />
        </div>
      </Card>
      <ErrorModal error={error} onClear={clearError} />
      {isLoading && (
        <div
          sx={{
            variant: "layout.center",
          }}
        >
          <LoadingSpinner />
        </div>
      )}
      {!isLoading && loadedUsers && <UsersList items={loadedUsers} />}
    </React.Fragment>
  );
};

export default Users;
