/** @jsx jsx */
/* eslint-disable */
import { jsx } from "theme-ui";
//import React from "react";
import { Link } from "react-router-dom";

const Button = (props) => {
  if (props.href) {
    return (
      <a
        sx={
          (props.danger && { variant: `buttons.btnCritical` }) ||
          (props.inverse && { variant: `buttons.btnMinor` }) || {
            variant: `buttons.btnMain`,
          }
        }
        href={props.href}
      >
        {props.children}
      </a>
    );
  }
  if (props.to) {
    return (
      <Link
        to={props.to}
        exact={props.exact}
        sx={
          (props.danger && { variant: `buttons.btnCritical` }) ||
          (props.inverse && { variant: `buttons.btnMinor` }) || {
            variant: `buttons.btnMain`,
          }
        }
      >
        {props.children}
      </Link>
    );
  }

  return (
    <button
      sx={
        (props.danger && { variant: `buttons.btnCritical` }) ||
        (props.inverse && { variant: `buttons.btnMinor` }) || {
          variant: `buttons.btnMain`,
        }
      }
      type={props.type}
      onClick={props.onClick}
      disabled={props.disabled}
    >
      {props.children}
    </button>
  );
};

export default Button;
