/** @jsx jsx */
/* eslint-disable */
import { jsx } from "theme-ui";
//import React from "react";
import { Link } from "react-router-dom";

import Avatar from "../../shared/components/UIElements/Avatar";
import Card from "../../shared/components/UIElements/Card";

const UserItem = (props) => {
  return (
    <li sx={{ variant: "styles.listWrapItem" }}>
      <Card sx={{ padding: 0 }}>
        <Link
          to={`/${props.id}/places`}
          sx={{ variant: "styles.listWrapItem.a" }}
        >
          <div sx={{ variant: "styles.listWrapItem.image" }}>
            <Avatar
              // image={`${process.env.REACT_APP_BACKEND_ASSET_URL}/${props.image}`}
              image={`${props.image}`}
              alt={props.name}
            />
          </div>
          <div sx={{ variant: "styles.listWrapItem.info" }}>
            <h2>{props.name}</h2>
            <h3>
              {/* {props.placeCount}  */}
              {/* {props.placeCount === 0 ? "No Info" : "With Info"} */}
            </h3>
          </div>
        </Link>
      </Card>
    </li>
  );
};

export default UserItem;
