import { useState, useCallback, useEffect } from "react";

let logoutTimer;

export const useAuth = () => {
  const [token, setToken] = useState(false);
  const [tokenExpirationDate, setTokenExpirationDate] = useState();
  const [userId, setUserId] = useState(false);

  // 'login' function is wrapped in 'useCallback' hook to prevent an infinite loop when the 'useEffect'
  // with the 'login' dependency runs
  const login = useCallback((uid, token, expirationDate) => {
    setToken(token);
    setUserId(uid);

    // 'tokenExpirationDate' variable below is scoped in this 'login' function only.
    // This is different from the 'tokenExpirationDate' state object.
    // If 'expirationDate' does not exists, generate a new value for this 'tokenExpirationDate' variable.
    const tokenExpirationDate =
      expirationDate || new Date(new Date().getTime() + 1000 * 60 * 60);
    setTokenExpirationDate(tokenExpirationDate);

    // save a 'userData' object (but converted to String) in the Local Storage
    localStorage.setItem(
      "userData",
      JSON.stringify({
        userId: uid,
        token: token,
        expiration: tokenExpirationDate.toISOString(),
      })
      // '.toISOString()' ensures that no data is lost when a date is converted to string and
      // it makes it easy to convert a string back to Date type
    );
  }, []);

  const logout = useCallback(() => {
    setToken(null);
    setTokenExpirationDate(null);
    setUserId(null);
    localStorage.removeItem("userData"); // Remove this variable with the token from the Local Storage
  }, []);

  // AUTO-LOGOUT
  // This 'useEffect' will run upon the rendering of this component and/or upon calling or update in its dependencies.
  // Start a Timer to get the remaining time for the token's expiration, and call the 'logout' function when
  // the time is up. Clear the Timer when the user logs out or when the token expires.
  useEffect(() => {
    if (token && tokenExpirationDate) {
      const remainingTime =
        tokenExpirationDate.getTime() - new Date().getTime();
      logoutTimer = setTimeout(logout, remainingTime);
    } else {
      clearTimeout(logoutTimer);
    }
  }, [token, logout, tokenExpirationDate]);

  // AUTO-LOGIN
  // This 'useEffect' will run upon the rendering of this component and/or upon the calling of the 'login'
  // method bound to the 'auth-context.js' Context hook.
  // The 'login' function defined here is wrapped in a 'useCallback' hook so that its contents will be cached.
  // If there is an unexpired token in the Local Storage, automatically log in the current user.
  useEffect(() => {
    // get the 'userData' variable stored in the Local Storage and convert to JSON
    const storedData = JSON.parse(localStorage.getItem("userData"));
    if (
      storedData &&
      storedData.token &&
      new Date(storedData.expiration) > new Date() // 'expiration' is still in the future
    ) {
      login(
        storedData.userId,
        storedData.token,
        new Date(storedData.expiration)
      );
    }
  }, [login]);

  return { token, login, logout, userId };
};
