import { useState, useCallback, useRef, useEffect } from "react";

export const useHttpClient = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState();

  // RAEL - 'useRef' hook will allow 'activeHttpRequests' to persist for the full lifetime of the component,
  // throughout re-renders.
  const activeHttpRequests = useRef([]);

  // RAEL - 'useCallback' with empty dependency to return its cached value (instead of a new return value)
  // when this function is re-executed, thus preventing an infinite loop when the calling function
  // is inside a 'useEffect' hook.
  const sendRequest = useCallback(
    async (url, method = "GET", body = null, headers = {}) => {
      setIsLoading(true);
      const httpAbortCtrl = new AbortController(); // ' AbortController' is built-in in modern browsers
      activeHttpRequests.current.push(httpAbortCtrl);

      try {
        const response = await fetch(url, {
          method,
          body,
          headers,
          signal: httpAbortCtrl.signal,
        });

        const responseData = await response.json();

        // RAEL - if there's no error, remove the current 'httpAbortCtrl' from the 'activeHttpRequests'
        // so that the completed request will not be pointlessly aborted
        activeHttpRequests.current = activeHttpRequests.current.filter(
          (reqCtrl) => reqCtrl !== httpAbortCtrl
        );

        if (!response.ok) {
          throw new Error(responseData.message);
        }

        setIsLoading(false);
        return responseData;
      } catch (err) {
        setError(err.message);
        setIsLoading(false);
        throw err;
      }
    },
    []
  );

  const clearError = () => {
    setError(null);
  };

  // RAEL - 'useEffect' with 'return' will ensure that its content will execute when this component
  // is unmounted, instead of during mounting
  useEffect(() => {
    return () => {
      // RAEL - abort the current request when the component is prematurely unmounted
      activeHttpRequests.current.forEach((abortCtrl) => abortCtrl.abort());
    };
  }, []);

  return { isLoading, error, sendRequest, clearError };
};
