/* eslint-disable */
// example theme with Typography.js
import { toTheme } from "@theme-ui/typography";
import wordpress2016 from "typography-theme-wordpress-2016";
import merge from "lodash.merge";

const typography = toTheme(wordpress2016);

export default merge(typography, {
  // optional style overrides go here
  breakpoints: ["40em", "56em", "64em"],
  space: [0, 4, 8, 16, 32, 64, 128, 256, 512],
  zIndices: [1, 5, 50, 100, 500, 600, 700, 800, 900, 1000],
  // fonts: {
  //   body:
  //     'system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", sans-serif',
  //   heading: "Georgia, serif",
  //   monospace: "Menlo, monospace",
  // },
  // fontSizes: [12, 14, 16, 20, 24, 32, 48, 64, 96],
  // fontWeights: {
  //   body: 400,
  //   heading: 700,
  //   bold: 700,
  // },
  // lineHeights: {
  //   body: 1.5,
  //   heading: 1.125,
  // },
  initialColorMode: "light",
  colors: {
    text: "#000",
    background: "#fff",
    primary: "#1b984f",
    secondary: "#034229",
    thirdColor: "#67e4a8",
    darkGray: "#343a4b",
    mainGray: "#eaebed",
    muted: "#f6f6f6",
    btnMainColor: "#008000",
    btnCriticalColor: "#003366",
    btnMinorColor: "#065535",
    btnMainHighlight: "#00b159",
    btnCriticalHighlight: "#00aedb",
    btnMinorHighlight: "#05ffa1",
    btnLinkHighlight: "#00ff00",
    btnLinkTextHighlight: "#292929",
    btnDisabledBgColor: "#ccc",
    btnDisabledTextColor: "#979797",
    cardColor: "#eaebed",
    cardTextColor: "#484d5d",
    modes: {
      dark: {
        text: "#857b8b",
        background: "#111111",
        foreground: "#333333",
        primary: "#075585",
        mainGray: "#adb0b7",
        btnMainColor: "#7a1212",
        btnMainHighlight: "#cc2411",
        btnMinorHighlight: "#a36d29",
        btnCriticalHighlight: "#09718b",
        btnLinkHighlight: "#1c741c",
        btnLinkTextHighlight: "#e3d2d2",
        cardColor: "#242834",
        cardTextColor: "#eaebed",
        // a36d29
      },
    },
  },
  // text: {
  //   heading: {
  //     fontFamily: "heading",
  //     lineHeight: "heading",
  //     fontWeight: "heading",
  //   },
  // },
  styles: {
    root: {
      background: "#4d4d4d",
      //     fontFamily: "body",
      //     lineHeight: "body",
      //     fontWeight: "body",
    },
    //   h1: {
    //     variant: "text.heading",
    //     fontSize: 5,
    //   },
    //   h2: {
    //     variant: "text.heading",
    //     fontSize: 4,
    //   },
    //   h3: {
    //     variant: "text.heading",
    //     fontSize: 3,
    //   },
    //   h4: {
    //     variant: "text.heading",
    //     fontSize: 2,
    //   },
    //   h5: {
    //     variant: "text.heading",
    //     fontSize: 1,
    //   },
    //   h6: {
    //     variant: "text.heading",
    //     fontSize: 0,
    //   },
    //   pre: {
    //     fontFamily: "monospace",
    //     overflowX: "auto",
    //     code: {
    //       color: "inherit",
    //     },
    //   },
    //   code: {
    //     fontFamily: "monospace",
    //     fontSize: "inherit",
    //   },
    table: {
      width: "100%",
      borderCollapse: "separate",
      borderSpacing: 0,
    },
    th: {
      textAlign: "left",
      borderBottomStyle: "solid",
    },
    td: {
      textAlign: "left",
      borderBottomStyle: "solid",
    },
    // custom header variant
    header: {
      width: "100%",
      height: 2,
      alignItems: "center",
      justifyContent: "spaceBetween",
      position: "fixed",
      top: 0,
      left: 0,
      bg: "primary",
      px: 0,
      py: 1,
      zIndex: 3,
    },
    // custom list variant
    listWrap: {
      listStyle: "none",
      mt: 2,
      mx: "auto",
      p: 0,
      width: "100%",
      // maxWidth: "40rem",
      maxWidth: "60rem",
      display: "flex",
      justifyContent: "center",
      flexWrap: "wrap",
    },
    listWrapItem: {
      m: "1rem",
      // width: "calc(45% - 2rem)",
      width: "calc(30% - 1rem)",
      // minWidth: "17.5rem",
      minWidth: "13.5rem",
      image: {
        // width: "4rem",
        // height: "4rem",
        width: "8rem",
        height: "8rem",
        mr: "1rem",
      },
      info: {
        m: 0,
        h2: {
          fontSize: "1.5rem",
          fontWeight: "normal",
          color: "cardTextColor",
          m: 0,
        },
        h3: {
          m: 0,
        },
      },
      a: {
        display: "flex",
        alignItems: "center",
        width: "100%",
        height: "100%",
        textDecoration: "none",
        padding: "1rem",
        color: "cardTextColor",
        bg: "cardColor",
        "&:hover": {
          bg: "btnLinkHighlight",
          h2: {
            color: "btnLinkTextHighlight",
          },
          h3: {
            color: "btnLinkTextHighlight",
          },
        },
        "&:active": {
          bg: "btnLinkHighlight",
          h2: {
            color: "btnLinkTextHighlight",
          },
          h3: {
            color: "btnLinkTextHighlight",
          },
        },
      },
    },
    listCenter: {
      listStyle: "none",
      my: "1rem",
      mx: "auto",
      p: 0,
      width: "90%",
      maxWidth: "40rem",
    },
    listCenterItem: {
      my: "1rem",
      mx: 0,
    },
    listCenterImage: {
      width: "100%",
      height: "100%",
      mr: "1.5rem",
    },
    listCenterImageImg: {
      width: "100%",
      height: "100%",
      objectFit: "cover",
    },
    listCenterHome: {
      width: "50%",
      height: "50%",
      mr: "1.5rem",
    },
    listCenterImageHome: {
      width: "50%",
      height: "50%",
      objectFit: "cover",
    },
    listCenterInfo: {
      p: "1rem",
      textAlign: "center",
      color: "btnLinkTextHighlight",
      h2: {
        mx: 0,
        mt: 0,
        mb: "0.5rem",
      },
      h3: {
        mx: 0,
        mt: 0,
        mb: "0.5rem",
      },
      p: {
        mx: 0,
        mt: 0,
        mb: "0.5rem",
      },
    },
    listCenterActions: {
      p: "1rem",
      textAlign: "center",
      borderTop: "1px solid #ccc",
    },
  },
  // custom link variant
  links: {
    bold: {
      fontWeight: "bold",
    },
    nav: {
      display: "flex",
      flexDirection: ["column", "column", "row"],
      justifyContent: "center",
      alignItems: "center",
      textDecoration: "none",
      listStyle: "none",
      width: "100%",
      height: "100%",
      margin: 0,
      padding: 0,
    },
    sidenav: {
      display: "flex",
      flexDirection: ["column", "column", "column"],
      justifyContent: "center",
      alignItems: "center",
      textDecoration: "none",
      listStyle: "none",
      width: "100%",
      height: "100%",
      margin: 0,
      padding: 0,
      lineHeight: "3rem",
    },
    li: {
      my: [2, 2, 0],
      mx: ["0.05rem", "0.05rem", "1rem"],
      textDecoration: "none",
    },
    a: {
      borderWidth: "1px",
      borderStyle: "solid",
      borderColor: "transparent",
      color: ["#292929", "#292929", "#292929"],
      textDecoration: "none",
      p: "0.5rem",
      "&:hover": {
        bg: "btnLinkHighlight",
        borderColor: "btnLinkTextHighlight",
        color: "btnLinkTextHighlight",
      },
      "&:active": {
        bg: "btnLinkHighlight",
        borderColor: "btnLinkTextHighlight",
        color: "btnLinkTextHighlight",
      },
      "&.active": {
        bg: "btnLinkHighlight",
        borderColor: "btnLinkTextHighlight",
        color: "btnLinkTextHighlight",
      },
    },
    btnLink: {
      borderWidth: "1px",
      borderStyle: "solid",
      borderColor: "white",
      color: ["btnLinkTextHighlight", "btnLinkTextHighlight", "white"],
      background: "transparent",
      px: "0.5rem",
      py: "0.25rem",
      font: "inherit",
      "&:hover": {
        bg: "btnLinkHighlight",
        color: "btnLinkTextHighlight",
      },
      "&:active": {
        bg: "btnLinkHighlight",
        color: "btnLinkTextHighlight",
      },
      "&.active": {
        bg: "btnLinkHighlight",
        borderColor: "btnLinkTextHighlight",
        color: "btnLinkTextHighlight",
      },
    },
  },
  // custom layout styles
  layout: {
    header: {
      color: "white",
      bg: "black",
    },
    footer: {
      bg: "gray",
    },
    sideDrawer: {
      position: "fixed",
      left: 0,
      top: 0,
      zIndex: 5,
      height: "100vh",
      width: "40%",
      background: "white",
      boxShadow: "0 2px 8px rgba(0, 0, 0, 0.26)",
    },
    backdrop: {
      position: "fixed",
      left: 0,
      top: 0,
      zIndex: 4,
      height: "100vh",
      width: "100%",
      background: "rgba(0, 0, 0, 0.75)",
    },
    center: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center",
    },
    card: {
      position: "relative",
      m: 0,
      p: 0,
      boxShadow: "0 2px 8px rgba(0, 0, 0, 0.26)",
      borderRadius: "6px",
      overflow: "hidden",
      bg: "background",
    },
    avatar: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      width: "100%",
      height: "100%",
      img: {
        display: "block",
        borderRadius: "50%",
        width: "100%",
        height: "100%",
        objectFit: "cover",
      },
    },
    authentication: {
      width: "90%",
      maxWidth: "25rem",
      my: "2rem",
      mx: "auto",
      textAlign: "center",
      // h2: { color: "black" },
      form: {
        mb: "1rem",
        // color: "black",
        bg: "background",
      },
    },
    placeForm: {
      listStyle: "none",
      my: 0,
      mx: "auto",
      p: "1rem",
      width: "90%",
      maxWidth: "40rem",
      boxShadow: "0 2px 8px rgba(0, 0, 0, 0.26)",
      borderRadius: "6px",
      bg: "background",
    },
    formControl: {
      my: "1rem",
      mx: 0,
      label: {
        display: "block",
        fontWeight: "bold",
        mb: "0.5rem",
      },
      input: {
        display: "block",
        width: "100%",
        font: "inherit",
        borderWidth: "1px",
        borderStyle: "solid",
        borderColor: "#ccc",
        bg: "#f8f8f8",
        py: "0.15rem",
        px: "0.25rem",
        "&:focus": {
          outline: "none",
          bg: "#ebebeb",
          borderColor: "#510077",
        },
      },
      textArea: {
        display: "block",
        width: "100%",
        font: "inherit",
        borderWidth: "1px",
        borderStyle: "solid",
        borderColor: "#ccc",
        bg: "#f8f8f8",
        py: "0.15rem",
        px: "0.25rem",
        "&:focus": {
          outline: "none",
          bg: "#ebebeb",
          borderColor: "#510077",
        },
      },
      imageUpload: {
        center: {
          display: "flex",
          justifyContent: "space-around",
          alignItems: "center",
          textAlign: "center",
        },
      },
      imageUploadPreview: {
        width: "13rem",
        height: "13rem",
        borderWidth: "1px",
        borderStyle: "solid",
        borderColor: "#ccc",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        textAlign: "center",
        mb: "1rem",
        img: {
          width: "100%",
          height: "100%",
          objectFit: "cover",
        },
      },
      //for the error message of imageUpload; needs fixing
      //p: { color: "red" },
    },
    formControlInvalid: {
      my: "1rem",
      mx: 0,
      label: {
        display: "block",
        fontWeight: "bold",
        mb: "0.5rem",
        color: "red",
      },
      p: { color: "red" },
      input: {
        display: "block",
        width: "100%",
        font: "inherit",
        borderWidth: "1px",
        borderStyle: "solid",
        borderColor: "red",
        bg: "#ffd1d1",
        py: "0.15rem",
        px: "0.25rem",
        "&:focus": {
          outline: "none",
          bg: "#ebebeb",
          borderColor: "#510077",
        },
      },
      textArea: {
        display: "block",
        width: "100%",
        font: "inherit",
        borderWidth: "1px",
        borderStyle: "solid",
        borderColor: "red",
        bg: "#ffd1d1",
        py: "0.15rem",
        px: "0.25rem",
        "&:focus": {
          outline: "none",
          bg: "#ebebeb",
          borderColor: "#510077",
        },
      },
    },
    mapContainer: {
      height: "17rem",
      width: "100%",
      p: 0,
      m: 0,
    },
  },
  sizes: {
    container: 768,
    s1: "1rem",
    s2: "2rem",
    s3: "3rem",
  },
  buttons: {
    primary: {
      color: "background",
      bg: "primary",
    },
    secondary: {
      color: "background",
      bg: "secondary",
    },
    // custom buttons
    btnBasic: {
      borderWidth: "1px",
      borderStyle: "solid",
      borderColor: "#ff0055",
      borderRadius: "4px",
      color: "white",
      py: "0.5rem",
      px: "1.5rem",
      mr: "1rem",
      font: "inherit",
      cursor: "pointer",
      textDecoration: "none",
      display: "inline-block",
    },
    btnMain: {
      variant: "buttons.btnBasic",
      borderColor: "transparent",
      bg: "btnMainColor",
      "&:hover": {
        bg: "btnMainHighlight",
      },
      "&:disabled": {
        bg: "btnDisabledBgColor",
        color: "btnDisabledTextColor",
        cursor: "not-allowed",
      },
    },
    btnMinor: {
      variant: "buttons.btnBasic",
      borderColor: "btnMinorColor",
      color: "btnMinorColor",
      bg: "transparent",
      "&:hover": {
        bg: "btnMinorHighlight",
      },
      "&:disabled": {
        bg: "btnDisabledBgColor",
        color: "btnDisabledTextColor",
        cursor: "not-allowed",
      },
    },
    btnCritical: {
      variant: "buttons.btnBasic",
      borderColor: "btnCriticalColor",
      bg: "btnCriticalColor",
      "&:hover": {
        bg: "btnCriticalHighlight",
      },
      "&:disabled": {
        bg: "btnDisabledBgColor",
        color: "btnDisabledTextColor",
        cursor: "not-allowed",
      },
    },
  },
});
