/** @jsx jsx */
/* eslint-disable */
import { jsx } from "theme-ui";
//import React from "react";
import { useContext } from "react";
import { NavLink } from "react-router-dom";
import ColorModeToggle from "./ColorModeToggle";

import { AuthContext } from "../../context/auth-context";

const SideNavLinks = (props) => {
  const auth = useContext(AuthContext);

  return (
    <ul
      sx={{
        variant: "links.sidenav",
      }}
    >
      <li
        sx={{
          variant: "links.li",
        }}
      >
        <ColorModeToggle></ColorModeToggle>
      </li>
      <li
        sx={{
          variant: "links.li",
        }}
      >
        <NavLink to="/" exact sx={{ variant: "links.a" }}>
          MEMBERS
        </NavLink>
      </li>
      {auth.isLoggedIn && (
        <li
          sx={{
            variant: "links.li",
          }}
        >
          <NavLink to={`/${auth.userId}/places`} sx={{ variant: "links.a" }}>
            MY INFO
          </NavLink>
        </li>
      )}
      {auth.isLoggedIn && (
        <li
          sx={{
            variant: "links.li",
          }}
        >
          <NavLink to="/places/new" sx={{ variant: "links.a" }}>
            ADD INFO
          </NavLink>
        </li>
      )}
      {!auth.isLoggedIn && (
        <li
          sx={{
            variant: "links.li",
          }}
        >
          <NavLink to="/auth" sx={{ variant: "links.a" }}>
            AUTHENTICATE
          </NavLink>
        </li>
      )}
      {auth.isLoggedIn && (
        <li
          sx={{
            variant: "links.li",
          }}
        >
          <button onClick={auth.logout} sx={{ variant: "links.btnLink" }}>
            LOGOUT
          </button>
        </li>
      )}
    </ul>
  );
};

export default SideNavLinks;
