/** @jsx jsx */
/* eslint-disable */
import { jsx } from "theme-ui";
//import React from "react";
import { useRef, useState, useEffect } from "react";

import Button from "./Button";

const ImageUpload = (props) => {
  const [file, setFile] = useState();
  const [previewUrl, setPreviewUrl] = useState();
  const [isValid, setIsValid] = useState(false);

  const filePickerRef = useRef(); // create a pointer to the File Input element

  useEffect(() => {
    // whenever a 'file' selection changes, show its preview image
    if (!file) {
      return;
    }
    const fileReader = new FileReader(); // 'FileReader' is built-in class in web browser
    fileReader.onload = () => {
      setPreviewUrl(fileReader.result); // set the URL of the selected image file
    };
    fileReader.readAsDataURL(file); // get the image file
  }, [file]);

  const pickedHandler = (event) => {
    let pickedFile;
    let fileIsValid = isValid;
    if (event.target.files && event.target.files.length === 1) {
      pickedFile = event.target.files[0];
      setFile(pickedFile); // set 'state' of the file name
      setIsValid(true);
      fileIsValid = true;
    } else {
      setIsValid(false);
      fileIsValid = false;
    }
    props.onInput(props.id, pickedFile, fileIsValid); // send back values to the Parent component - 'Auth.js'
  };

  const pickImageHandler = () => {
    filePickerRef.current.click(); // trigger 'click' event of the referenced input element when this handler is called
  };

  return (
    <div sx={{ variant: `layout.formControl` }}>
      <input
        id={props.id}
        ref={filePickerRef} // assign a ref pointer to this input element
        style={{ display: "none" }} // hide this file input element
        type="file"
        accept=".jpg,.png,.jpeg"
        onChange={pickedHandler} // this is triggered when the <Button> is clicked using 'useRef'
      />
      <div
        sx={
          props.center && { variant: `layout.formControl.imageUpload.center` }
        }
      >
        <div sx={{ variant: `layout.formControl.imageUploadPreview` }}>
          {previewUrl && <img src={previewUrl} alt="Preview" />}
          {!previewUrl && <p>Please pick an image.</p>}
        </div>
        <Button type="button" onClick={pickImageHandler}>
          PICK IMAGE
        </Button>
      </div>
      {!isValid && <p>{props.errorText}</p>}
    </div>
  );
};

export default ImageUpload;
